.form-container {
    display: flex;
    max-width: 100%;
    /* height: 1200px; */
    position: fixed;
    overflow: hidden;
    font-family: Arial, sans-serif;
  }

  .form-step input::placeholder {
    color: rgba(53, 53, 53, 0.5);
    font-family: Manrope;
    font-size: 18px;
    font-weight: 500;
    line-height: 24.59px;
    letter-spacing: 0.15em;
    padding: 12px 20px;
    text-align: left;
  }
  
  .left-panel {
    flex: 1;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 980px;
  }
  
  /* h2 {
    position: absolute;
    top: 75px;
    left: 32px;
    font-family: Americana BT;
    font-size: 32px;
    font-weight: 700;
    line-height: 37.69px;
    text-align: left;
    color: #FFFFFF;
  
  
  } */
  
  .layout {
    width: 1440px;
    height: 80px;
  }
  
  .logo {
    width: 78px;
    height: 64px;
    position: relative;
    top: 8px;
    left: 60px;
    right: 30px;
    gap: 0px;
    border-radius: 8px 0px 0px 0px;
    border: 3px 0px 0px 0px;
    opacity: 0px;
  
  }
  
  .form-image {
    width: 400px;
    height: 980px;
    position: absolute;
    top: 82px;
    left: 10px;
    gap: 0px;
    opacity: 0px;
    /* rotation: -180 deg; */
  }
  
  .right-panel {
    top: 35px;
    right:120px;
    flex: 2;
    padding: 30px;
    position: relative;
    font-size: 13px;
    
  }
  
  .stepper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
  }
  
  .step {
    flex: 1;
    padding: 30px;
    text-align: center;
    border-bottom: 4px solid #ccc;
  }
  
  .step.completed {
    border-bottom: 4px solid #4D2800;
  }
  
  .step.active {
    border-bottom: 4px solid #4D2800;
    font-weight: bold;
  }
  
  .form-step {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
  }
  
  .form-step h3 {
    margin-bottom: 25px;
    color: #4D2800;
    font-size: 15px;
  }
  
  .form-step input {
    width: 100%;
    padding: 12px 24px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  /* label{
    margin-left: 1px;
  } */
  
  .button-group {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    width: 100%;
  }
  
  .button-group button {
    padding: 20px 50px;
    border: none;
    border-radius: 5px;
    background-color: #4D2800;
    color: #fff;
    cursor: pointer;
  }
  
  .button-group button:hover {
    background-color: #333;
  }